.articles{
    background-color:hsl(220, 16%, 96%);
    padding: 10px 10vw 5% 10vw;
    margin-top: -3%;
}

.latestarticles{
    font-size: 2rem;
    margin: 5% 0;
}

.articles-list{
    display: flex;
    flex-direction: row;
    gap: 3%;
}

.article-text{
    background-color: white;
    padding: 5%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.article-head:hover{
    cursor: pointer;
    color: hsl(136, 65%, 51%);
}

.author,.article-tag{
    color: hsl(233, 8%, 62%);
}

.article-img>img{
    width: 100%;
    height: 100%;
    aspect-ratio: 16 / 9;
    border-top-left-radius: 5%;
    border-top-right-radius: 5%;
}

@media screen and (max-width:788px) {
    .articles-list{
        flex-direction: column;
        gap: 20px;
    }
}