.home{
    /* background-color: hsl(0, 0%, 98%);  */
    display: flex;
    justify-content: space-around;
    position: relative;
    overflow: hidden;
}

.homeimage{
    background-image: url("/public/images/bg-intro-desktop.svg");
    background-size: 100%;
    background-position: -2rem 1rem;
    background-repeat: no-repeat;
    width: 60%;
    position: relative;
    left: 10%;
    margin-top: -8%;
    z-index: 10;
}

.left-home{
    padding: 0 0 0 10vw;
    width: 50%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    gap: 5%;
}

.title{
    font-size: 3rem;
    font-weight: 300;
}

.tag{
    color: hsl(233, 8%, 62%);
    line-height: 1.75;
}

@media screen and (max-width:788px) {
    .home{
        flex-direction: column-reverse;
    }
    .left-home{
        padding: 5%;
        width: 100vw;
        text-align: center;
        gap: 20px;
        align-items: center;
        margin-top: -10rem;
    }
    .title{
        font-size: 2rem;
        font-weight: 400;
    }
    .homeimage{
        background-image: url("/public/images/bg-intro-mobile.svg");
        background-size: 100%;
        background-position: 100% 200%;
        padding: 5%;
        width: 100%;
        left: 0;
        margin-top: -25%;
    }
    .home.invite-buttom{
        margin: 100px;
    }
}