.footer{
    background-color: hsl(233, 26%, 24%);
    padding: 10px 10vw 0 10vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

a{
    text-decoration: none;
    color: hsl(233, 8%, 85%);   
    margin: 10px;
}

.copyright{
    color: hsl(233, 8%, 62%);
    text-align: center;
}

svg path:hover{
    cursor: pointer;
    fill: hsl(136, 65%, 51%);
}

.left-footer{
    display: flex;
    justify-content: space-between;
    width: 60%;
}

.footer-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto 0;
}

.links{
    width: 100%;
    padding: 10% 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.right-footer{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}

@media screen and (max-width:788px) {
    .footer{
        flex-direction: column;
        width: 100%;
    }
    .left-footer{
        flex-direction: column;
    }
    .finvite.invite-button{
        display: block;
    }
    .right-footer{
        gap: 25px;
        margin-top: 25px;
        margin-bottom: 50px;
    }
    .links{
        width: 70%;
    }
}