.features{
    background-color: hsl(233, 8%, 95%);
    padding: 10px 10vw 0 10vw;
    margin-top: -10%;
    padding-bottom: 5%;
}

.features-top{
    width: 70%;
}

.features-top>div{
    font-size: 2.5rem;
    font-weight: 300;
    margin: 7% 0 5% 0;
}

.features-top>p{
    width: 80%;
    font-size: 1rem;
    color: hsl(233, 8%, 62%);
    margin-bottom: 5%;
}

.features-list{
    display: flex;
    flex-direction: row;
    margin: 5% 0;
}

.feature-head{
    font-size: large;
    font-weight: 400;
    margin: 10% 0;
}

.feature-tag{
    color: hsl(233, 8%, 62%);
}


@media screen and (max-width:788px) {
    .features{
        margin-top: -20%;
        text-align: center;
        width: 100vw;
    }
    .features-top{
        width: 100%;
        padding: 10px;
        text-align: center;
    }
    .features-top>p{
        width: 100%;
        line-height: 1.5;
    }
    .features-list{
        flex-direction: column;
        width: 100%;
        gap: 2rem;
    }
    .features-list>*{
        width: 100%;
    }
    .feature-head{
        margin: 5% 0;
    }
}