.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px 10vw 0 10vw;
  z-index: 10;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 100;
}

.hamburger {
  display: none;
}
.header-link{
  color: hsl(233, 8%, 62%);
}
.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.nav > div {
  padding: 1rem 0;
  margin: 0 1rem;
  color: hsl(233, 8%, 62%);
  border-bottom: 4px solid transparent;
  text-align: center;
}

.nav > div:hover {
  color: black;
  border-bottom: 4px solid;
  border-image: linear-gradient(
    to right,
    hsl(136, 65%, 51%),
    hsl(192, 70%, 51%)
  );
  border-image-slice: 1;
  cursor: pointer;
}

.invite-button {
  text-align: center;
  color: white;
  background-image: linear-gradient(
    to right,
    hsl(136, 65%, 51%),
    hsl(192, 70%, 51%)
  );
  padding: 10px 15px;
  border-radius: 20px;
  width: max-content;
}

.invite-button:hover {
  opacity: 0.6;
  cursor: pointer;
}
@media screen and (max-width: 788px) {
  .hamburger {
    display: block;
  }
  .invite-button {
    display: none;
  }
  .header {
    justify-content: space-between;
    padding: 1.5rem;
  }
  .nav.close{
    display: none;
  }
  .nav.mobilenav {
    background-color: white;
    width: 80vw;
    border-radius: 10px;
    flex-direction: column;
    position: absolute;
    top: 5rem;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    z-index: 10;
  }
  .nav > div {
    color: black;
    padding: 0.7rem 0;
  }
}
